import "./style.scss"
import React from "react";
import {Prism as SyntaxHighlighter} from 'react-syntax-highlighter';
import {dark} from 'react-syntax-highlighter/dist/esm/styles/prism';

export function ResponseBody({responseBody, contentType}) {
    let bodyText = ""
    const [prettify, setPrettify] = React.useState(false)
    if (responseBody) {
        bodyText = responseBody;
        if (prettify) {
            bodyText = JSON.stringify(JSON.parse(bodyText), null, 2)
        }
    }
    let language = 'javascript'
    if (contentType === 'application/javascript') {
        language = 'javascript';
    } else if (contentType === 'text/html') {
        language = 'html';
    }

    return <>
        <textarea
            className={"responseBody"}
            readOnly={true}
            value={bodyText}>
             <SyntaxHighlighter language={language} style={dark}>
                 {bodyText}
            </SyntaxHighlighter>
        </textarea>
        <input id={"prettify"} type={"checkbox"} onChange={(e) => setPrettify(e.target.checked)}/>
        <label htmlFor={"prettify"}>Prettify</label>
    </>
}